.page-header__nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 100px;
}

.page-header__menu {
  ul {
    display: flex;
    gap: 50px;

    li {
      a {
        position: relative;
        padding: 0px 0px 10px 0px;

        &:after {
          position: absolute;
          bottom: -2px;
          left: 0;
          right: 0;
          margin: auto;
          width: 0%;
          content: '';
          color: transparent;
          background: rgb(255, 255, 255);
          height: 2px;
          transition: all 0.3s;
          -webkit-transition: all 0.3s;
          -moz-transition: all 0.3s;
          -ms-transition: all 0.3s;
          -o-transition: all 0.3s;
        }

        &:hover:after {
          width: 100%;
        }
      }
    }
  }
}

.js-menu-link {
  color: white;
  font-size: 18px;
}

.active-header-nav {
  border-bottom: 2px solid white;
}

@media (max-width: 981px) {
  .page-header__menu {
    ul {
      display: flex;
      gap: 40px;
      align-items: center;
    }
  }
}

@media (max-width: 916px) {
  .js-menu-link {
    color: white;
    font-size: 14px;
  }
  
}
